@import "variables";

@font-face {
  font-family: 'ymf';
  src:  url('#{$icomoon-font-path}/ymf.eot?x1hfil');
  src:  url('#{$icomoon-font-path}/ymf.eot?x1hfil#iefix') format('embedded-opentype'),
    url('#{$icomoon-font-path}/ymf.ttf?x1hfil') format('truetype'),
    url('#{$icomoon-font-path}/ymf.woff?x1hfil') format('woff'),
    url('#{$icomoon-font-path}/ymf.svg?x1hfil#ymf') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="i-"], [class*=" i-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'ymf' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.i-ymf {
  &:before {
    content: $i-logo;
  }
}
.i-ymf1 {
  &:before {
    content: $i-logo1;
  }
}

