@import '../../node_modules/susy/sass/susy';
@import 'reset.css';
@import 'style';
$susy: (
  global-box-sizing:border-box
);
.abs-full {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}

body {
  @extend .abs-full;
  font-family: 'Microsoft YaHei';
  overflow: hidden;
}

$nh: 4em;
.nav {
  position: relative;
  color: #eee;
  background: transparentize(#000, .5);
  padding: 0 2em;
  height: $nh;
  line-height: $nh;
  z-index: 10;
  a, span {
    text-decoration: none;
    color: inherit;
  }
  span {
    position: relative;
  }
  .subm {
    background: transparentize(#000, .3);
    display: inline-block;
    width: 12em;
    position: absolute;
    top: -1+$nh;
    right: -3em;
    transition: max-height .6s ease-in-out;
    overflow: hidden;
    max-height: 0;
    a {
      width: 100%;
      display: block;
      line-height: 3em;
      &:hover {
        background: transparentize(#000, .5);
      }
      &:before {
        display: none !important;
      }
    }
  }
  [data-i="1"] {
    .subm {
      max-height: 100em;
    }
  }
  .i-ymf.logo,.i-ymf1.logo {
    position: relative;
    font-size: 3em;
    line-height: $nh / 3;
  }
  .title {
    float: left;
    line-height: inherit;
  }
  $menuW: 15em;
  @include susy-breakpoint(0 30em) {
    $x: (100 / 11)*1%;
    .nav-btn {
      z-index: 10;
      margin: 1em -1em 1em 1em;
      float: right;
      height: 2em;
      width: 2em;
      border-radius: 3px;
      position: relative;
      cursor: pointer;
      transition: .3s ease-in-out;
      &[data-act="1"] {
        transform: translate3d(-11.2em, 0, 0);
        span {
          background: #aaa !important;
          &:nth-child(1) {
            transform: rotate(45deg);
            top: $x*3;
          }
          &:nth-child(2) {
            opacity: 0;
          }
          &:nth-child(3) {
            transform: rotate(-45deg);
            top: -$x*3;
          }
        }
      }
      span {
        position: relative;
        transition: .3s;
        transform: translate3d(0, 0, 0);
        display: table;
        margin: $x*2 auto;
        background: transparentize(#fff, .4);
        width: 80%;
        height: $x;
      }
    }
    .nav-btn[data-act="1"] + .menu {

      overflow-y: auto;
      right: 0;
      .msk {
        z-index: auto;
        pointer-events: auto;
        background: transparentize(#000, .9);
      }
    }
  }
  .menu {
    transform: translate3D(0, 0, 0);
    float: right;
    .msk {
      transition: .3s;
      position: fixed;
      left: -100em;
      top: 0;
      bottom: 0;
      right: 0;
      background: transparentize(#000, 1);
      z-index: -1;
      pointer-events: none;
    }
    a, span {
      text-decoration: none;
      color: #aaa;
      position: relative;
      padding: 0 1em;
      cursor: pointer;
      &.act, &:hover {
        color: #ddd;
      }
      + a:before, + span:before {
        position: absolute;
        display: block;
        content: '';
        left: 0;
        width: 1px;
        height: 100%;
        top: 0;
        background: currentColor;
      }
    }
    @include susy-breakpoint(0 30em) {
      & {
        width: $menuW;
        position: fixed;
        top: 0;
        padding-top: $nh;
        right: -1-$menuW;
        bottom: 0;
        background: #1c2127;
        box-shadow: rgba(0, 0, 0, 0.68) 0 -0.5em 1em;
        transform: translate3d(0, 0, 0);
        transition: .3s;
        a, span {
          width: 100%;
          line-height: 4em;
          display: block;
          box-sizing: border-box;
          &:nth-child(2) {
            border-top: 1px solid rgba(0, 0, 0, 0.7);
          }
          &:hover {
            background: #2a323b;
          }
          padding: 0 2em;
          color: #aaa !important;
          &:before {
            display: none !important;
          }
        }
        span {
          z-index: 1;
          .subm {
            position: relative;
            float: left;
            left: 0;
            top: 0;
            right: 0;
            width: $menuW;
            margin: 0 -2em;
            background-color: #1c2127;
            a {
              border: none !important;
              &:hover {
                background-color: #2a323b;
              }
            }
          }
        }
      }
    }
  }
}

.carousel {;
  @extend .abs-full;
  overflow: hidden;
  .box {
    height: 100%;
    width: 100%;
    background: #333;
    > div {
      @extend .abs-full;
      opacity: 0;
      transition: 1s all ease-in-out;
      height: 100%;
      width: 100%;
      background: no-repeat center;
      background-size: cover;
    }
  }
  .index {
    position: relative;
    display: table;
    margin: -3em auto 3em;
    z-index: 1;
    .act {
      width: 4em;
      height: .5em;
      position: absolute;
      top: 0;
      transition: all .3s ease-in-out;
      background: transparentize(#55350f, .3);
      pointer-events: none;
      z-index: 10;
    }
    span {
      z-index: 1;
      float: left;
      display: block;
      width: 4em;
      height: .5em;
      background: transparentize(#fff, .7);
      margin: 0 1em;
      cursor: pointer;
    }
    @include susy-breakpoint(0 30em) {
      span, .act {
        width: .5em;
      }
      span {
        margin: 0 .5em;
      }
    }
  }
  .ctrl {
    position: absolute;
    top: 50%;
    width: 100%;
    z-index: 1;
    div {
      transition: .2s all ease-in-out;
      height: 4em;
      width: 4em;
      border-radius: 3px;
      opacity: .5;
      background: transparentize(#000, .8);
      position: absolute;
      cursor: pointer;
      top: 0;
      &:hover {
        opacity: 8;
        background: transparentize(#000, .5);
      }
      &:after {
        content: '';
        display: block;
        width: 2em;
        height: 2em;
        border-radius: 3px;
        border: 1px solid #fff;
        transform: rotate(45deg);
        position: absolute;
        top: .9em;
      }
      &.left {
        left: 1em;
        &:after {
          left: 1.4em;
          border-right: 0;
          border-top: 0;
        }
      }
      &.right {
        right: 1em;
        &:after {
          right: 1.4em;
          border-left: 0;
          border-bottom: 0;
        }
      }
    }
  }
}

.imgs {
  .h3 {
    font-size: 2em;
    padding: 1em 0;
    text-align: center;
    color: #2a323b;
  }
  box-sizing: border-box;
  position: relative;
  height: 100%;
  overflow: auto;
  width: 100%;
  padding: 5em 2% 1em;
  &:after {
    clear: both;
    content: '';
    display: block;
  }
  $pic-height: 15em;
  .pic {
    height: $pic-height;
    position: relative;
    float: left;
    padding: 1em .5em 2em;
    box-sizing: border-box;
    &.a {
      width: 25%
    }
    &.b {
      width: 25%
    }
    img{
      max-height: 100%;
      max-width: 100%;
      margin: auto;
      display: table;
    }
    @include susy-breakpoint(0 50em) {
      & {
        padding: .2em 0;
      }
      &.a, &.b {
        width: 100%;
      }
    }
    .img {
      box-shadow: rgba(0, 0, 0, 0.1) .1em .1em .4em;
      display: block;
      width: 100%;
      border: 1px solid rgba(0, 0, 0, 0.1);
      border-radius: 3px;
      height: -3 + $pic-height;
      vertical-align: middle;
      background:  no-repeat center;
      background-size: contain;
      position: relative;
      .m {
        display: block;
        text-decoration: none;
        opacity: 0;
        transition: .3s ease-in-out;
        height: 100%;
        width: 100%;
        background: transparentize(#000, .6);
        line-height: $pic-height/7*3;
        text-align: center;
        color: #fff;
        font-size: 2em;
        a {
          color: #fff;
          text-decoration: none;
          font-size: .6em;
          padding: .4em 1em;
          position: relative;
          transition: .8s ease-in-out;
          i {
            width: 2px;
            height: 2px;
            display: block;
            position: absolute;
            transition: .5s ease-in-out;
          }
          i:nth-child(1), i:nth-child(2) {
            top: 0
          }
          i:nth-child(3), i:nth-child(4) {
            bottom: 0;
          }
          i:nth-child(1), i:nth-child(3) {
            left: 0
          }
          i:nth-child(2), i:nth-child(4) {
            right: 0
          }
          &:hover {
            i {
              background: #55350f
            }
            color: #55350f;
            background-color: #fff;
            i:nth-child(1), i:nth-child(4) {
              width: 100%
            }
            i:nth-child(2), i:nth-child(3) {
              height: 100%
            }
          }
        }
      }
      &:hover {
        .m {
          opacity: 1;
        }
      }
    }
    .name{
      font-size: .8em;
      text-align: center;
      margin: .5em 0 2em;
    }
  }
}
figure.pic{
  .img{
    display: flex!important;
    align-items: center;
    justify-content: center;
    @include susy-breakpoint(0 50em){
      width: 100%;
    }
  }
}
.foo {
  clear: both;
  padding: 1em 2em 3.5em;
  box-sizing: border-box;
  width: 100%;
  position: fixed;
  z-index: 0;
  bottom: 0;
  .info {
    background-color: transparentize(#000, .5);
    font-size: .8em;
    line-height: 1.5;
    padding: 1em;
    color: #fff;
    float: right;
    .concat {
      float: left;
    }
    .code {
      margin-left: 2em;
      height: 6em;
      width: 6em;
      background-size: cover;
      float: right;
    }
  }
}

body {
  @include susy-breakpoint(0 50em) {
    overflow: auto;
  }
}
.base1 {
  position: fixed;
  width: 100%;
  z-index: 100;
  .nav {
    background: #fff;
    color: #333333;
    border-bottom: #dddddd 1px solid;
    box-shadow: transparentize(#000, .9) 0 .1em .3em;
    .nav-btn {
      span {
        background: #666;
      }
    }
    a, span {
      color: #3d3d3d;
      &.act, &:hover {
        color: #333;
      }
    }
    .subm {
      background-color: #fff;
      box-shadow: transparentize(#000, .9) 0 .1em .3em;
      a {
        &:first-child {
          border-top: 1px solid #eee;
        }
        &:hover {
          background: #eee;
        }
      }
    }
  }
  ~* .foo {
    position: relative;
    padding:0 0 1em;
    .info {
      box-sizing: border-box;
      background-color: #fff;
      color: #1c2127;
    }
  }
  +.box1{
    .foo{
      .info {padding-bottom: 2em}
    }
  }
}

.content:after {
  content: '';
  display: block;
  clear: both;
}

.box1 {
  position: absolute;
  top: $nh;
  bottom: 0;
  width: 100%;
  left: 0;
  overflow: auto;
  @include susy-breakpoint(0 50em) {
    position: relative;
    padding-top: 20em;
  }
}

.banner, .about {
  min-height: 100%;
  width: 50%;
  padding: 2em 2em 2em;
  box-sizing: border-box;
  .cp {
    width: 80%;
    margin: 0 auto;
    line-height: 1.5;
    color: #555;
    h1.i-ymf{
      font-size: 6em;
      line-height: 1;
      text-align: center;
      padding: .5em 0;
    }
    .pic1 {
      width: 100%;
      padding-top: 369/923*100%;
      margin: 4em 0 1em;
    }
  }
  &.b2 .cp p {
    padding-left: 1em;
  }
  @include susy-breakpoint(0 50em) {
    min-height: initial;
    padding-top: 1em !important;
    width: 100%;
  }
}

.td {
  width: 100%;
  height: 3.5em;
  background: 0 50% no-repeat;
  background-size: auto 100%;
  margin: 1em 0 2em;
}

.pic2 {
  width: 100%;
  padding-top: 70%;
  background: center no-repeat;
  background-size: contain;
  margin: 4em 0 2em;
}

.banner {
  background: center no-repeat;
  transform: translate3d(0, 0, 0);
  position: fixed;
  top: $nh;
  z-index: 0;
  transition: .6s ease-in-out background;
  .bt {
    transition: opacity .3s ease-in-out;
    width: 100%;
    height: 100%;
    background: no-repeat 100% 10%;
    background-size: 20% !important;
  }
  &[data-i="0"] {
    background: #eee url('https://i03.gitee.io/images/bg1.jpg') center;
    .bt {
      opacity: 0;
    }
  }
  &[data-i="1"] {
    background: #eee url('https://i03.gitee.io/images/bg3.png') center;
    .bt {
      opacity: 1;
    }
  }
  @include susy-breakpoint(0 50em) {
    height: 20em;
  }
  background-size: cover !important;
}

.about {
  display: inline-block;
  margin-left: 50%;
  @include susy-breakpoint(0 50em) {
    margin-left: 0;
    background: #fff;
    position: relative;
    z-index: 2;
    padding: 0;
    box-shadow: 0 -.2em 3em rgba(0, 0, 0, .3);
    height: auto;
    & + div.banner {
      display: block;
    }
    .cp {
      padding-bottom: 5em;
    }
  }
  & + .banner {
    display: none;
    position: relative;
    top: 0;
    float: right;
    background-color: #eee;
  }
}

#pg {
  width: 90%;
  position: relative;
  margin: 0 auto 0em;
  white-space: nowrap;
  display: flex;
  justify-content: center;
  text-align: center;
  &:after {
    content: '';
    display: block;
    clear: both;
  }
}

.pg {
  float: left;
  margin: 1em .5em;
  min-width: 1em;
  text-align: center;
  cursor: pointer;
  color: #999;
  &.dis {
    color: #aaa;
    cursor: default;
  }
  &.act {
    color: #333;
  }
}

$fh: 3em;


.pswp{
  top:$nh;
}
